import React from 'react';
import {Button, Div, Group} from "@vkontakte/vkui";
import {Icon28ChevronRightOutline, Icon28UserOutline} from "@vkontakte/icons";
import {NavLink} from "react-router-dom";
import Persik from "../Persik";

const Buttons = ({go}) => {
    return (
        <Group className="Persik" mode={"card"}>
            <Div>
                <NavLink to='/prof'><Button className="buton" size="l" mode="secondary" expandable
                                            before={<Icon28UserOutline/>}>Профиль</Button></NavLink>
                <NavLink to='/Curs1'><Button className="buton" size="l" mode="secondary" expandable
                                             before={<Icon28ChevronRightOutline/>}>1 Курс</Button></NavLink>
                <NavLink to='/Curs2'><Button className="buton" size="l" mode="secondary" expandable
                                             before={<Icon28ChevronRightOutline/>}>2 Курс</Button></NavLink>
                <NavLink to='/Curs3'><Button className="buton" size="l" mode="secondary" expandable
                                             before={<Icon28ChevronRightOutline/>}>3 Курс</Button></NavLink>
                <NavLink to='/Curs4'><Button className="buton" size="l" mode="secondary" expandable
                                             before={<Icon28ChevronRightOutline/>}>4 Курс</Button></NavLink>
                <NavLink to='/Curs5'><Button className="buton" size="l" mode="secondary" expandable
                                             before={<Icon28ChevronRightOutline/>}>5 Курс</Button></NavLink>
            </Div>
        </Group>
    );
};

export default Buttons;